"use client";

import Link from "next/link";
import { useState } from "react";
import { signIn } from "next-auth/react";
import { useRouter } from "next/router";

export default function LoginForm() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();
        
        try {
            const res = await signIn(credentials, {
                email,
                password,
                redirect:false,
            });

            if (res.error) {
                setError("Invalid Credentials!");
                return;
            }
        } catch (error) {
            
        }
    };

    return (
        <div className="grid place-items-center h-screen">
            <div className="shadow-lg p-5 rounded-lg border-t-4 border-green-400">
                <h1 className="text-xl font-bold my-4">Student Information System</h1>
                <h2>Log in to your account</h2>

                <form onSubmit={handleSubmit} className="flex flex-col gap-3 mt-2">
                    <input onChange={e => setEmail(e.target.value)} type="text" placeholder="Email"/>
                    <input onChange={e => setPassword(e.target.value)} type="password" placeholder="Password"/>
                    <button className="bg-green-700 text-white font-bold cursor-pointer px-6 py-2">Login</button>
                   
                    {error && (
                        <div className="bg-red-600 text-white w-fit text-sm py-1 px-3 rounded-md mt-2">
                            {error}
                        </div>
                    )}

                    <Link className="text-sm mt-3 text-right" href={"/register"}>
                        Don't have an account? <span className="underline">Sign up here</span>
                    </Link>
                </form>
            </div>
        </div>
    );
}